@import './../../styles/vars.sass';
@import './../../styles/media.sass';

.input {
    border-radius: 10px;
    font-size: 16px;
    border: 1px solid lightgray;
    background: white;
    padding: 10px;
    &::placeholder {
        color: lightgray; } }
.button {
    border: 0;
    background: $gradient;
    color: white;
    border-radius: 10px;
    padding: 12px;
    font-size: 14px;
    position: relative;
    font-weight: bold;
    cursor: pointer;
    overflow: hidden;
    &:hover:after {
        transform: skewX(-42deg) translateX(140px);
        transition: .6s ease; }
    &:after {
        position: absolute;
        content: '';
        width: 40px;
        bottom: 0;
        background: linear-gradient(-92deg,rgba(255,255,255,.4),rgba(255,255,255,.4));
        height: 120%;
        left: 0;
        transform: skewX(-42deg) translateX(-50px);
        transform: skewX(-42deg) translateX(-50px);
        z-index: 1;
        will-change: transform; } }
.add {
    padding: 20px $padding;
    @include sm() {
        padding: 20px $sm-padding; }
    box-shadow: 0 0px 15px rgba(1,1,1,0.04);
    &__desc {
        font-size: 14px;
        color: rgba(0,0,0,.6);
        line-height: 0.9;
        margin-bottom: 5px; }
    &__container {
        display: flex;
        flex-wrap: wrap;
        margin: -5px; } }
.input-group {
    & > .input {
        flex: auto;
        max-width: 310px;
        margin: 5px; }
    & > .button {
        margin: 5px; } }


